import { useEffect, useState } from 'react';
import axios from 'axios';

function OrderModal({ closeModal, getCoupons, type, tempCoupon }) {//fn
    const [tempData, setTempData] = useState({
        title: "",
        is_enabled: 1,
        percent: 80,
        due_date: 1555459200,
        code: "testCode"
    });
    const [date, setDate] = useState(new Date())

    useEffect(() => {
        console.log(type, tempCoupon);
        if (type === 'edit') {
            setTempData(tempCoupon)
            // console.log(type, tempCoupon);
            setDate(new Date(tempCoupon.due_date));
        }
    }, [type, tempCoupon]);

    const handleChange = (e) => {
        console.log(e);
        const { value, name } = e.target;
        if (['price', 'origin_price'].includes(name)) {//namepriceorigin_price
            setTempData({
                ...tempData,
                [name]:Number(value),
            })
        } else if (name === 'is_enabled') {
            setTempData({
                ...tempData,
                [name]: +e.target.checked, //boolean +
            })
        } else {
            setTempData({
                ...tempData,
                [name]:value,
            })
        }
    }

    const submit = async () => {
        try {
            let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon`; //typeapi
            let method = 'post';
            if (type ==='edit') {//typeapi
                api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${tempCoupon.id}`;
                method = 'put';
            }
            const res =  await axios[method](
                api, 
                {
                    data: {
                        ...tempData,
                        due_date: date.getTime(),//  unix timestamp
                    },
                },
            );
            console.log(res)
            closeModal();
            getCoupons();
        } catch (error) {
            console.log(error)
        }
    }
    return (
        <div
            className='modal fade'
            tabIndex='-1'
            id='orderModal'
            aria-labelledby='exampleModalLabel'
            aria-hidden='true'
        >
            <div className='modal-dialog modal-lg'>
                <div className='modal-content'>
                    <div className='modal-header'>
                        <h1 className='modal-title fs-5' id='exampleModalLabel'>
                            {` ${tempData.id}`}
                        </h1>
                        <button
                            type='button'
                            className='btn-close'
                            aria-label='Close'
                            // onClick={closeProductModal}
                        />
                    </div>
                    <div className='modal-body'>
                        <div className='mb-3 row'>
                            <span className='col-sm-2 col-form-label'>Email</span>
                            <div className='col-sm-10'>
                                <input
                                    type='email'
                                    readOnly
                                    className='form-control-plaintext'
                                    id='staticEmail'
                                    // defaultValue={tempOrder?.user?.email}
                                />
                            </div>
                        </div>
                        <div className='mb-3 row'>
                            <span className='col-sm-2 col-form-label'></span>
                            <div className='col-sm-10'>
                                <input
                                    type='text'
                                    readOnly
                                    className='form-control-plaintext'
                                    id='staticEmail'
                                    // defaultValue={tempOrder?.user?.name}
                                />
                            </div>
                        </div>
                        <div className='mb-3 row'>
                            <span className='col-sm-2 col-form-label'></span>
                            <div className='col-sm-10'>
                                <input
                                    type='text'
                                    readOnly
                                    className='form-control-plaintext'
                                    // defaultValue={tempOrder?.user?.address}
                                />
                            </div>
                        </div>
                        <div className='mb-3 row'>
                            <span className='col-sm-2 col-form-label'></span>
                            <div className='col-sm-10'>
                                <textarea
                                    name=''
                                    id=''
                                    cols='30'
                                    readOnly
                                    className='form-control-plaintext'
                                    // defaultValue={tempOrder.message}
                                />
                            </div>
                        </div>
                        {/* {tempOrder.products && ( */}
                            <table className='table'>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {/* {Object.values(tempOrder.products).map((cart) => (
                                        <tr key={cart.id}>
                                            <td>{cart.product.title}</td>
                                            <td>{cart.qty}</td>
                                        </tr>
                                    ))} */}
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td className='border-0 text-end'></td>
                                        {/* <td className='border-0'>${tempOrder.total}</td> */}
                                    </tr>
                                </tfoot>
                            </table>
                        {/* )} */}

                        <div>
                            <h5 className='mt-4'></h5>
                            <div className='form-check mb-4'>
                                <label className='form-check-label' htmlFor='is_paid'>
                                    <input
                                        className='form-check-input'
                                        type='checkbox'
                                        name='is_paid'
                                        id='is_paid'
                                        checked={!!tempData.is_paid}
                                        onChange={handleChange}
                                        // disabled={isLoading}
                                    />
                                     ({tempData.is_paid ? '' : ''})
                                </label>
                            </div>
                            <div className='mb-4'>
                                <span className='col-sm-2 col-form-label d-block'>
                                    
                                </span>
                                <select
                                    className='form-select'
                                    name='status'
                                    value={tempData.status}
                                    onChange={handleChange}
                                    // disabled={isLoading}
                                >
                                    <option value={0}></option>
                                    <option value={1}></option>
                                    <option value={2}></option>
                                    <option value={3}></option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div className='modal-footer'>
                        <button
                            type='button'
                            className='btn btn-secondary'
                            onClick={closeModal}
                        >
                            
                        </button>
                        <button type='button' className='btn btn-primary' onClick={submit}>
                            
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default OrderModal;